.aside {
  display: flex;
  gap: 10px;
  float: left;
  height: 450px;
  padding-left: 5px;

  margin-right: 30px;
  outline: 1px solid var(--color-border);
}
.listIcons {
  padding: 3px 0px 0px 0px;
}
.listText li {
  color: var(--color-text);
}
.listText {
  padding-top: 3px;
}
.svg * {
  fill: var(--color-text);
  stroke: var(--color-text);
  g: var(--color-text);
}
